  max-width: 500px;
  align-self: flex-start;
}
@media (max-width: 1199px) {
  .main-footer__nav {
    min-width: 381px;
  }
}
@media (max-width: 767px) {
  .main-footer__nav {
    min-width: 290px;
    max-width: none;
    position: relative;
    align-self: start;
    grid-column: 1/3;
    grid-row: 1/2;
  }
}

.main-footer__nav-list {
  max-width: 764px;
}
@media (max-width: 1199px) {
  .main-footer__nav-list {
    max-width: 520px;
  }
}
@media (max-width: 767px) {
  .main-footer__nav-list {
    padding: 0;
  }
}

.main-footer__social {
  align-self: flex-end;
}
@media (max-width: 767px) {
  .main-footer__social {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}

.main-footer__logo:hover,
.main-footer__logo:focus {
  opacity: 0.7;
}

.main-footer__logo:active {
  opacity: 0.3;
}

.user-list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 40px;
  padding: 0;
  margin: 0;
  padding-left: 8px;
}
@media (max-width: 1199px) {
  .user-list {
    column-gap: 30px;
    padding-left: 0;
    margin-left: -10px;
  }
}
@media (max-width: 767px) {
  .user-list {
    display: grid;
    gap: 16px;
    padding-left: 12px;
    column-gap: 27px;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: ". promotion" "services coachеs" "subscriptions timetable";
  }
}

@media (max-width: 767px) {
  .user-list__item--promotion {
    grid-area: promotion;
    grid-row: 1/2;
    grid-column: 2/3;
    margin-top: -4px;
  }
  .user-list__item--footer {
    grid-area: services;
    grid-row: 2/3;
    grid-column: 1/2;
  }
  .user-list__item--coachеs {
    grid-area: coachеs;
    grid-row: 2/3;
    grid-column: 2/3;
  }
  .user-list__item--subscriptions {
    grid-area: subscriptions;
  }
  .user-list__item--timetable {
    grid-area: timetable;
    grid-column: 2/3;
    grid-row: 3/4;
  }
}

.user-list__link {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
}
.user-list__link:hover, .user-list__linkfocus {
  opacity: 0.7;
}
.user-list__link:active {
  opacity: 0.5;
}
@media (max-width: 1199px) {
  .user-list__link {
    font-size: 14px;
    line-height: 16px;
  }
}
@media (max-width: 767px) {
  .user-list__link {
    line-height: 21px;
    letter-spacing: 1px;
  }
}

.social__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 11px;
  padding: 0;
  margin: 0;
  max-width: 128px;
}
@media (max-width: 767px) {
  .social__list {
    margin-top: 34px;
  }
}

@media (max-width: 1199px) {
  .social__network {
    width: 16px;
    height: 16px;
  }
}
@media (max-width: 767px) {
  .social__network {
    width: 25px;
    height: 25px;
  }
}

.social__link:hover,
.social__link:focus {
  opacity: 0.7;
}

.social__link:active {
  opacity: 0.3;
}

